import { Component, OnInit } from '@angular/core';
import docs from "../assets/documentation.json";


export enum Tabs {
	overview,
	license,
	apireference,
	samples
};

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.scss']
})

export class AppComponent {
	title = 'app';
	CurrentTab: Tabs;
	TabTypes = Tabs;

	constructor() { 
	}

	async ngOnInit() {
		this.CurrentTab = Tabs.overview;
	}	

	goRestpyRepo() {
		window.open('https://github.com/OpenIxia/ixnetwork_restpy');
	}

	goRestpyPyPi() {
		window.open('https://pypi.org/project/ixnetwork-restpy');
	}

	public get license(): string {
		return docs['LICENSE'];
	}

	public get version(): string {
		return docs['VERSION'];
	}
}
